.webuploader-container {
	position: relative;
}
.webuploader-element-invisible {
	position: absolute !important;
	clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
    clip: rect(1px,1px,1px,1px);
}
.webuploader-pick {
	position: relative;
	display: inline-block;
	cursor: pointer;
	/*background: #00b7ee;*/
	background: var(--emColor);
  height: 36px;
	padding: 0 10px;
  font-size: 14px;
	color: #fff;
  line-height: 36px;
	text-align: center;
	border-radius: 3px;
	overflow: hidden;
}
.webuploader-pick-hover {
	/*background: #00a2d4;*/
	background: var(--emColor-dark);
}

.webuploader-pick-disable {
	opacity: 0.6;
	pointer-events:none;
}
/*众筹发布图片上传*/
.btn-imgUpload .webuploader-pick {
  width:100%;
  height: 100%;
  opacity: 0;
}





